.login-dark {
  height: auto;
  background: #475d62 url(../../asset/login-bg.jpg);
  background-size: cover;
  position: relative;
}

.login-dark form {
  max-width: 320px;
  width: 90%;
  background-color: #1e2833;
  padding: 40px;
  border-radius: 4px;
  transform: translate(-50%, -50%);
  position: absolute;
  top: 50%;
  left: 50%;
  color: #fff;
  box-shadow: 3px 3px 4px rgba(0, 0, 0, 0.2);
}

.login-dark .illustration {
  text-align: center;
  padding: 15px 0 20px;
  font-size: 100px;
  color: #2980ef;
}

.login-dark form .form-control {
  background: none;
  border: none;
  border-bottom: 1px solid #434a52;
  border-radius: 0;
  box-shadow: none;
  outline: none;
  color: inherit;
}

.login-dark form .btn-primary {
  background: #214a80;
  border: none;
  border-radius: 4px;
  padding: 11px;
  box-shadow: none;
  margin-top: 26px;
  text-shadow: none;
  outline: none;
}

.login-dark form .btn-primary:hover,
.login-dark form .btn-primary:active {
  background: #214a80;
  outline: none;
}

.login-dark form .forgot {
  display: block;
  text-align: center;
  font-size: 12px;
  color: #6f7a85;
  opacity: 0.9;
  text-decoration: none;
}

.login-dark form .forgot:hover,
.login-dark form .forgot:active {
  opacity: 1;
  text-decoration: none;
}

.login-dark form .btn-primary:active {
  transform: translateY(1px);
}

#overlay {
  background: rgba(43, 45, 66, 0.82);
  z-index: 2;
}

.main-head {
  font-size: 35px;
}

.brand-login {
  font-size: 20px !important;
}

.login-button {
  width: 108px !important;
}

@media (min-width: 1025px) {
  .main-head {
    font-size: 80px !important;
  }
}

@media (min-width: 576px) {
  .main-head {
    font-size: 70px;
  }

  .brand-login {
    font-size: 30px !important;
  }

  .login-button {
    width: 150px !important;
  }

  .guest {
    margin-right: 8px !important;
  }
}

@media (max-width: 942px) {
  .details {
    -ms-flex: 0 0 100%;
    flex: 0 0 100%;
    max-width: 100%;
  }
}
